import React from "react";

export default function Comments({ siteUrl, pagePath }) {
  const scriptContent = `
  var disqus_config = function () {
    this.page.url = '${siteUrl}${pagePath}';
    this.page.identifier = '${pagePath}';
  };

  (function() { // DON'T EDIT BELOW THIS LINE
    var d = document, s = d.createElement('script');
    s.src = 'https://leogcrespo.disqus.com/embed.js';
    s.setAttribute('data-timestamp', +new Date());
    (d.head || d.body).appendChild(s);
  })();
  `;

  return [
    <div key={0} id="disqus_thread" />,
    <script key={1} dangerouslySetInnerHTML={{ __html: scriptContent }} />,
    <noscript key={2}>
      Please enable JavaScript to view the{" "}
      <a href="https://disqus.com/?ref_noscript">comments powered by Disqus.</a>
    </noscript>
  ];
}



